.app {
  background-color: black;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;

  .container {
    background-color: rgb(45, 97, 209);
    border-radius: 20px;
    flex-basis: 90%;
    max-width: 980px;
    padding: 90px;
  
    .app-title {
      margin-bottom: 100px;
      font-size: 40px;
      text-align: center;
      font-family: Arial, Helvetica, sans-serif;
    }
    .btn-list {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-around;
      .btn-item {
        
      }
    }
  }
}